<template>
    <div>
        <router-link v-bind:to="'../boke_detail/'+id">返回</router-link>
        <h1>这是一个博客页面</h1>
        <div id="boke" v-if="!sucess">
            <p>编辑博客</p>
            <form>
                <label>博客标题</label>
                <input type="text" v-model="blog.title">
                <label>博客内容</label>
                <textarea v-model="blog.content"></textarea>
                <div class="check">
                    <label>Vue.js</label>
                    <input type="checkbox" value="Vue.js" v-model="blog.cate">
                    <label>Node.js</label>
                    <input type="checkbox" value="Node.js" v-model="blog.cate">
                    <label>React.js</label>
                    <input type="checkbox" value="React.js" v-model="blog.cate">
                    <label>Angular.js</label>
                    <input type="checkbox" value="Angular.js" v-model="blog.cate">
                </div>
                <div class="author">
                    <label>作者</label>
                    <select v-model="blog.author">
                        <option  v-for="authors in author" >{{authors}}</option>
                    </select>
                </div>
            </form>
            <button v-on:click.prevent="bianji">提交编辑内容</button>
        </div>
        <h3 v-if="sucess">
            博客发布成功了
        </h3>

    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "boke",
        data(){
            return{
                id:this.$route.params.id,
                sucess:false,
                blog:{},
                author:['张三','李四','王五','赵六']
            }
        },
        created(){
            axios.get('https://wd1742575853wzelpf.wilddogio.com/post/'+this.id+'.json').then((data)=>{
                return data.data;

            }).then((data)=>{
                this.blog=data

            })
        },
        methods:{
            bianji:function(){
                axios.put('https://wd1742575853wzelpf.wilddogio.com/post/'+this.id+'.json',this.blog).then((response)=>{
                    console.log(response)
                    this.blog=response.body;
                    this.sucess=true
                })
            }
        }

    }
</script>

<style scoped>
    #boke *{box-sizing: border-box;text-align: left}
    #boke{width: 600px;margin:0 auto}
    #boke p{font-size: 24px;padding: 10px auto}
    #boke form{display: block;width: 100%}
    #boke>form>label{display: block;width: 100%;font-size: 20px}
    input[type='text'],textarea{display: block;width: 100%;margin: 20px auto;border: 1px solid #000;outline: none;height: 30px}
    #boke textarea{height: 200px;}
    .check label{display: inline-block;}
    .check input[type='checkbox']{margin-right: 20px}
    .author{width: 100%;margin: 20px auto}
    .author label{display: block;margin: 20px auto 10px;font-size: 20px}
    .author select{display: block;width: 100%;height: 30px}
    #boke button{display: block;width: 120px;height: 40px;background: red;color: #fff;border-radius: 4px;text-align: center;border: none;outline: none}
    .boke-right{position: absolute;top:100px;right: 20px;width: 300px;border: 2px solid #000}
    .boke-right p{float: left;width:300px;text-align: left;overflow: hidden;white-space: normal}
    .boke-right p:nth-child(2){border-bottom: 1px solid #999}
    .boke-right span{padding: 20px;display: block}
</style>


