<template>
    <footer>
        <h2>{{foot}}{{title}}</h2>
    </footer>
</template>

<script>
    export default {
        name: "app-footer",
        props:{
            title:{
                type:String
            }
        },
        data(){
            return{
                foot:'这是网站的尾部'
            }
        }
    }
</script>

<style scoped>
footer{width: 100%;padding: 30px 0;background: green;color: #fff;}
</style>


