// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App'
import HelloWord from './components/HelloWord'
import Home from './components/Home'
import boke from './components/boke'
import showboke from './components/showboke'
import Boke_detail from './components/Boke_detail'
import Bianji from './components/Bianji'



Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.directive('supercase',{
    bind(el,binding,vnode){
        el.style.color='#'+Math.random().toString(16).slice(2,8)
    }
})
Vue.directive('bian',{
    bind(el,binding,vnode){
        if(binding.value=='wide'){
            el.style.width='1200px'
        }else if(binding.value=='zai'){
            el.style.width='600px'
        }
    }
});
// Vue.filter('uppercase',function(value){
//     return value.toUpperCase()
// });
Vue.filter('shenglue',function(value){
    return value.slice(0,100)+'...'
})
const router = new VueRouter({
    routes: [
        {path: "/", component: Home},
        {path: "/helloword", component: HelloWord},
        {path: "/boke", component: boke},
        {path: "/showboke", component: showboke},
        {path: "/boke_detail/:id", component: Boke_detail},
        {path: "/bianji/:id", component: Bianji},
    ],
    mode: "history"
})
/* eslint-disable no-new */
new Vue({
    router,
    el: '#app',
    components: {App},
    template: '<App/>'
}).$mount('#app')



