<template>
    <div class="boke_detail">
        <router-link to="/showboke">返回博客列表页</router-link>
        <div class="detail">
            <h2>{{blog_detail.title}}</h2>
            <p>{{blog_detail.content}}</p>
            <p v-for="cate in blog_detail.cate">分类{{cate}}</p>
            <p>作者{{blog_detail.author}}</p>
        </div>
        <button v-on:click="delet">删除</button>
        <router-link v-bind:to="'/bianji/'+id">编辑</router-link>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "boke_detail",
        data(){
            return{
                id:this.$route.params.id,
                blog_detail:{}
            }
        },
        created(){
            axios.get('https://wd1742575853wzelpf.wilddogio.com/post/'+this.id+'.json').then(function(data){
                return data.data;

            }).then((data)=>{
                this.blog_detail=data

            })
        },
        methods:{
            delet:function(){
                this.$http.delete('https://wd1742575853wzelpf.wilddogio.com/post/'+this.id+'.json').then((response)=>{
                   this.$router.push({path:'/showboke',query:{alert:'此条数据删除成功'}})
                })
            }
        }
    }
</script>

<style scoped>
    .detail{width: 600px;margin: 0 auto;background: #eee;padding: 30px 20px}
    .detail h2{text-align: center;padding: 10px 0;font-size: 28px}
    .detail p{text-align: left;text-indent: 2rem;line-height: 36px;font-size: 18px}

</style>


