<template>
    <div id="apps">
        <ul>
            <li v-for="content in contentqq">
                <p>{{content.name}}</p>
                <p>{{content.email}}</p>
            </li>
            <button v-on:click="deleteContent"></button>
        </ul>
    </div>
</template>

<script>

    export default {
        name: 'apps',
        props:{
            contentqq:{
                type:Array,
                required:true
            }
        },
        methods:{
            deleteContent:function () {
                this.contentqq.pop()
            }
        },
        components: {

        }
    }
</script>

<style>
    *{
        list-style: none;
    }
    #apps {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
    ul{
        width: 100%;
        max-width: 1200px;
        height: auto;
        overflow: hidden;
    }
    ul li{
        float: left;
        width: 200px;
        margin-left: 40px;
    }
</style>



