<template>
    <div id="showboke">
        <router-link to="./boke">博客添加页面</router-link>
        <div class="showboke" v-bian="'zai'">
            <p>搜索内容</p>
            <input type="text" placeholder="搜索内容" v-model="search">
            <div class="showConotent" v-for="content in filterblog">
                <router-link v-bind:to="'/boke_detail/'+content.id">
                    <span v-supercase>{{content.title | uppercase}}</span>
                </router-link>
                <p>{{content.content | shenglue}}</p>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "showboke",
        data(){
            return{
                content:[],
                search:''
            }
        },
        created(){
            axios.get('https://wd1742575853wzelpf.wilddogio.com/post.json').then(function(data){
                return data.data;
            }).then((data)=>{
                var newarr=[]
                for(let key in data){
                    data[key].id=key;
                    newarr.push(data[key])
                }
                this.content=newarr
            })
        },
        computed:{
            filterblog:function(){
                return this.content.filter((blog)=>{
                    return blog.title.match(this.search)
                })
            }
        },
        filters:{
            'uppercase':function(value){
            return value.toUpperCase()
        }
        }
    }

</script>

<style scoped>
    a{list-style: none}
    .showboke{width: 600px;margin: 0 auto}
    .showboke>p{font-size: 22px;text-align: left}
    .showboke input[type='text']{display: block;width: 100%;height: 30px;border: 1px solid #ccc;outline: none;margin: 10px 0;padding-left: 10px}
    .showConotent{padding: 20px;margin: 20px;background: #ccc}
    .showConotent span{display: block;width: 100%;text-align: left;font-size: 20px;cursor: pointer}
    .showConotent p{width: 100%;text-align: left}
</style>


