<template>
    <div id="home">
        <ul>
             <li> <router-link to="/helloword">二级页面</router-link></li>
             <li> <router-link to="/boke">博客页面</router-link></li>
        </ul>
        <app-header v-on:changContent="already($event)" v-bind:title="title"></app-header>
        <apps v-bind:contentqq="content"></apps>
        <apps v-bind:contentqq="content"></apps>
        <app-footer v-bind:title="title"></app-footer>
    </div>
</template>

<script>

    import apps from './Tom';
    import footer from './footer';
    import header from './header';
    export default {
        name: 'home',
        data(){
            return{
                title:'公共内容',
                content:[
                    // {name:"小明",age:"11",show:false},
                    // {name:"小明",age:"11",show:false},
                    // {name:"小明",age:"11",show:false},
                    // {name:"小明",age:"11",show:false},
                    // {name:"小明",age:"11",show:false}
                ]
            }
        },
        methods:{
            already:function (lasttitle) {
                this.title=lasttitle;
            }
        },
        created(){
            this.$http.get('http://jsonplaceholder.typicode.com/users').then((data)=>{
                this.content=data.body
            })
        },
        components: {
            'Apps':apps,
            'app-footer':footer,
            'app-header':header,
        }
    }
</script>

<style>
    #home{
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
</style>



