        // },
        // beforeUpdate:function () {
        //     alert('第五、组建更新前，页面仍未更新，但虚拟DOM已经配置')
        // },
        // updated:function () {
        //     alert('第六、组建更新后，此方法执行后，页面显示')
        // },
        // beforeDestroy:function () {
        //     alert('组建销毁前')
        // },
        // destroy:function () {
        //     alert('组建销毁后')
        // }
    }
</script>

<style scoped>
    header{width: 100%;padding: 30px 0;background: red;color: #fff;}
</style>


